<template>
    <div class='board'>
        <h1>
          {{ user }}
          <button is='btn' type='success' @click="fetchData()">Atualizar</button>
        </h1>

        <div class="github-cards">
          <div is='card' v-for='repo in repos'
            :name='repo.name'
            :description='repo.description'
            :language='repo.language'
            :stargazers-count='repo.stargazers_count'
            :open-issues-count='repo.open_issues_count'
            :created='repo.created_at'
            :pushed='repo.pushed_at'
            />
        </div>
    </div>
</template>

<script>
import Card from './Card'
import Btn from './Button'

export default {
  name: 'board',
  data () {
    return {
      user: 'wilfernandesjr',
      repos: []
    }
  },
  mounted () {
    this.fetchData()
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      this.$http.get('https://api.github.com/users/wilfernandesjr/starred')
        .then(response => {
          this.repos = response.body
        }, response => {
          this.$http.get('starred.json')
            .then(response => {
              this.repos = response.body
            })
        })
    }
  },
  components: {
    Card,
    Btn
  }
}
</script>

<style scoped>
.github-cards {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  width: 100%;
}
</style>



