<template>
  <a href="https://github.com/Nexmo/nexmo-ruby" class="github-card" data-github="Nexmo/nexmo-ruby">
    <h3>{{ name }}</h3>
    <p>{{ description }}</p>
    <span class="github-card__meta">
      <span class="github-card__language-icon" style="color: #7A0410;">●</span> {{ language }}
    </span>
    <span class="github-card__meta">
      {{ stargazersCount }}
    </span>
    <span class="github-card__meta">
      {{ openIssuesCount }}
    </span>
    <span class="github-card__meta">
      {{ created }}
    </span>
    <span class="github-card__meta">
      {{ pushed }}
    </span>
    <span class="github-card__meta">
      <i class="fa fa-star" aria-hidden="true"></i>
      <span data-stars>
        <i class="fa fa-spinner" aria-hidden="true"></i>
      </span>
    </span>
    <span class="github-card__meta">
      <i class="fa fa-code-fork" aria-hidden="true"></i>
      <span data-forks>
        <i class="fa fa-spinner" aria-hidden="true"></i>
      </span>
    </span>
  </a>
</template>

<script>
export default {
  name: 'card',
  props: [
    'name',
    'description',
    'language',
    'stargazers-count',
    'open-issues-count',
    'created',
    'pushed'
  ],
  data () {
    return {
      repoName: 'REPO NAME'
    }
  }
}
</script>

<style scoped>
.github-card {
  display: block;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin: 10px;
  padding: 20px;
  color: #555;
  text-decoration: none;
  font-size: 13px;
  flex: 1;
  min-width: 250px;
}
.github-card > h3 {
  margin-top: 0;
  color: #4078c0;
  font-size: 15px;
}

.github-card__meta {
  margin-right: 20px;
}
.github-card__meta > i {
  font-size: 16px;
}
</style>



